.d2-ui-table__headers {
  display: table-row; }

.d2-ui-table__headers__header {
  height: 50px;
  padding: 0 1.6rem;
  vertical-align: middle;
  border-bottom: 2px solid #E0E0E0;
  color: #757575;
  display: table-cell;
  font-weight: 400;
  text-align: left;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease; }

.d2-ui-table__rows__row {
